import { Component, OnInit, OnDestroy  } from '@angular/core';
import { MySpecialLoggerService } from "../service/my-special-logger.service";


@Component({
  selector: 'app-body',
  templateUrl: './body.component.html',
  styleUrls: ['./body.component.css'],
  host: {'class': 'col-12 col-sm-6 col-md-4 col-lg-3 '}
  
})
export class BodyComponent implements OnInit {

  title: string = 'Body!!';
  curVal = 0;
  manualVal = 0;
  static LIMIT_CNT = 100;
  // @Input() logger: MySpecialLoggerService;

  constructor(private logger: MySpecialLoggerService) { }

  ngOnInit() { }

  onClick(element){
    alert(element.value);
  }

  colorByValue() {
    if (this.curVal > 0) return 'green';
    else if (this.curVal < 0) return 'red';
    else return 'grey'; 
  }

  inc() {
    const tempVal = this.curVal + 1;
    if (this.checkLimitCnt(tempVal)) {
      this.curVal = tempVal;
    }
    this.logger.debug("ss");
  }

  dec() {
    const tempVal = this.curVal - 1;
    if(this.checkLimitCnt(tempVal)) {
      this.curVal = tempVal;
    }
  }

  setValueForcibly() {
    if (this.checkLimitCnt(this.manualVal)) {
      this.curVal = this.manualVal;
    }
  }

  checkLimitCnt(val) {
    if (Math.abs(val) < BodyComponent.LIMIT_CNT) {
      return true;
    }

    const target = val > 0 ? '증가' : '감소';
    alert(`더 이상 ${target}시킬 수 없습니다.`);
    return false;

  }

}



